.About {
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: #1c1c1c;
}

.About-inner {
  position: relative;
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.About-more-indicator {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 0;
  height: 0;
  border-bottom: 5rem solid #eee;
  border-left: 5rem solid transparent;
  cursor: pointer;
  z-index: 10;
}

.About-more-indicator .fa-info {
  position: absolute;
  bottom: -70px;
  right: 5px;
  width: 30px;
  height: 30px;
  color: rgb(238, 93, 9);
  -webkit-animation: blink 1.5s linear alternate infinite;
          animation: blink 1.5s linear alternate infinite;
}

.About-more-container {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  bottom: 0;
  right: 0;
  width: 3rem;
  height: 3rem;
  border-top-left-radius: 100%;
  z-index: 5;
  background-color: #212121;
  -webkit-transition-timing-function: cubic-bezier(0.34, 0.99, 0.36, 1);
       -o-transition-timing-function: cubic-bezier(0.34, 0.99, 0.36, 1);
          transition-timing-function: cubic-bezier(0.34, 0.99, 0.36, 1);
  -webkit-transition-property: width, height, border-top-left-radius;
  -o-transition-property: width, height, border-top-left-radius;
  transition-property: width, height, border-top-left-radius;
  -webkit-transition-duration: 1s, 1s, 1.5s;
       -o-transition-duration: 1s, 1s, 1.5s;
          transition-duration: 1s, 1s, 1.5s;
}

.expanded {
  width: 100%;
  height: 100%;
  border-top-left-radius: 0;
  opacity: 1;
}

.About-more-text {
  -ms-flex: 2 1;
      flex: 2 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;
  height: 70vh;
  padding-right: 4rem;
  visibility: hidden;
}

.About-more-text p {
  max-width: 30vw;
  font-size: 1.2rem;
  overflow: hidden;
  max-height: 0;
}

.About-more-text p:nth-child(1) {
  -webkit-transition: max-height 0.5s ease-in-out 1s;
  -o-transition: max-height 0.5s ease-in-out 1s;
  transition: max-height 0.5s ease-in-out 1s;
}

.About-more-text p:nth-child(2) {
  -webkit-transition: max-height 0.5s ease-in-out 1.5s;
  -o-transition: max-height 0.5s ease-in-out 1.5s;
  transition: max-height 0.5s ease-in-out 1.5s;
}

.About-more-text p:nth-child(3) {
  -webkit-transition: max-height 0.5s ease-in-out 2s;
  -o-transition: max-height 0.5s ease-in-out 2s;
  transition: max-height 0.5s ease-in-out 2s;
}

.About-more-text p:nth-child(4) {
  -webkit-transition: max-height 0.5s ease-in-out 2.5s;
  -o-transition: max-height 0.5s ease-in-out 2.5s;
  transition: max-height 0.5s ease-in-out 2.5s;
}

.expanded .About-more-text {
  visibility: visible;
}

.expanded .About-more-text p {
  max-height: 6rem;
}

.About-more-photos-wrapper {
  -ms-flex: 3 1;
      flex: 3 1;
  height: 70vh;
  visibility: hidden;
}

.About-more-photos {
  position: relative;
  width: 70%;
  height: 100%;
}

.expanded .About-more-photos-wrapper {
  visibility: visible;
}

.About-more-photos::after {
  content: '';
  background-color: #212121;
  position: absolute;
  height: calc(100% / 2.5);
  width: calc(100% / 11.5);
  z-index: 5;
}

.About-more-photos::before {
  content: '';
  background-color: #212121;
  position: absolute;
  height: calc(100% / 3.286);
  width: calc(100% / 3.286);
  right: 0;
  z-index: 5;
}

.About-more-photos .mask {
  position: absolute;
  background-color: #1c1c1c;
  opacity: 0.55;
  width: 0;
  z-index: 5;
  -webkit-transition: width 0.5s ease-in-out 1s;
  -o-transition: width 0.5s ease-in-out 1s;
  transition: width 0.5s ease-in-out 1s;
}

.About-more-photos .mask0 {
  left: 0;
  top: calc(100% / 2.5);
  height: calc(100% - 100%/2.5);
}

.About-more-photos .show .mask0 {
  width: 40%;
}

.About-more-photos .mask1 {
  left: 0;
  top: calc(100% / 1.5);
  height: calc(100% - 100%/1.5);
}

.About-more-photos .show .mask1 {
  width: 100%;
}

.About-more-photos .mask2 {
  right: 0;
  top: calc(100% / 3.286);
  height: calc(100% / 3.286);
}

.About-more-photos .show .mask2 {
  width: calc(100% / 3.286);
}

.About-more-photos .mask3 {
  left: 0;
  top: 0;
  height: calc(100% / 2.5);
}

.About-more-photos .show .mask3 {
  width: 100%;
}

.About-more-photos .show {
  z-index: 0;
  width: 100%;
}

.About-more-photos .hide {
  width: 0;
  -webkit-transition-delay: 1s;
       -o-transition-delay: 1s;
          transition-delay: 1s;
  z-index: -1;
}

.About-more-photos li {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 0;
  -webkit-filter: brightness(0.6) grayscale(100%);
          filter: brightness(0.6) grayscale(100%);
  -webkit-transition: width 1s cubic-bezier(0.55, 0.33, 0.01, 1.01);
  -o-transition: width 1s cubic-bezier(0.55, 0.33, 0.01, 1.01);
  transition: width 1s cubic-bezier(0.55, 0.33, 0.01, 1.01);
}

.About-more-photos li:nth-child(1) {
  background-image: url('./images/show1.jpg');
}

.About-more-photos li:nth-child(2) {
  background-image: url('./images/show2.jpg');
}

.About-more-photos li:nth-child(3) {
  background-image: url('./images/show3.jpg');
}

.About-more-photos li:nth-child(4) {
  background-image: url('./images/show4.jpg');
}

.About-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}

.About-section p {
  z-index: 2;
  width: 70vw;
  font-size: 1.5rem;
}

.About-section__quote {
  font-style: italic;
  margin: 0;
  -ms-flex: 1 1;
      flex: 1 1;
}

.About-section__quote p {
  font-size: 1.2rem;
  margin: 0;
}

.About-section__quote p:last-child {
  text-align: right;
}

.About-section-parallax {
  width: 100%;
  height: 100%;
  position: absolute;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-filter: brightness(0.45) grayscale(50%);
          filter: brightness(0.45) grayscale(50%);
}

.parallax-1 {
  -ms-flex: 3 1;
      flex: 3 1;
}

.parallax-1 .About-section-parallax {
  background-image: url('./images/coding1.jpg');
}

.parallax-2 {
  -ms-flex: 4 1;
      flex: 4 1;
}

.parallax-2 .About-section-parallax {
  background-image: url('./images/coding2.jpg');
}

@-webkit-keyframes blink {
  from {
    opacity: 0.2;
  }
  to {
    opacity: 1;
  }
}

@keyframes blink {
  from {
    opacity: 0.2;
  }
  to {
    opacity: 1;
  }
}



