.Header {
  position: fixed;
  top: 4rem;
  left: -4rem;
  -webkit-transition: left 0.3s ease-in-out;
  -o-transition: left 0.3s ease-in-out;
  transition: left 0.3s ease-in-out;
  z-index: 10;
}

.Header.show-logo {
  left: 0;
}

.Header-logo {
  font-family: 'Lobster', cursive;
  font-size: 1.6rem;
  height: 4rem;
  width: 4rem;
  outline: none;
}



